.editor {
  --editor-left-width: 30px;
  --editor-frame-width: 60%;

  display: flex;
  flex-direction: row;
  flex-grow: 1;
  margin-top: 3px;
}

.frame {
  transition: transform linear .15s;
}

.frameMini {
  transform: translate(calc(100% * var(--iframe-translate)), calc(100% * var(--iframe-translate))) scale(var(--iframe-scale));
}

.left {
  width: var(--editor-left-width);
}

.center {
  box-sizing: border-box;
  width: var(--editor-frame-width);

  display: flex;
  flex-direction: column;
  background-color: #FFFFFF;
}

.right {
  box-sizing: border-box;
  width: calc((100% - var(--editor-frame-width)) - var(--editor-left-width));
}
