.slidePanel {
  position: fixed;
  background-color: #394b59;
  transition: left linear .15s;
}

.grid {
  padding: 16px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  grid-gap: 16px;
  background-color: #394b59;
  justify-items: center;
}

.itemCard {
  width: 150px;
  height: 150px;
  outline: none;
  padding: 4px;
}

.itemInner {
  height: 100%;
}
