._frame_body {
  margin: 0;
}

._frame_slot_component {
  cursor: pointer;
  min-width: 40px;
  background: repeating-radial-gradient(
    #CB2EE7,
    #48BB78
    20%
  );
}

._frame_slot {
  cursor: pointer;
  min-width: 40px;
  background: repeating-radial-gradient(
    var(--slot-col1, #e66465), var(--slot-col2, #9198e5) var(--slot-width, 20%)
  );
}

._frame_slot_active {
  animation: _frame_slot_kf 2s ease-out infinite alternate;
}

._frame_slot_waiting {
  --slot-col1: #81E6D9;
  --slot-col2: #48BB78;
}

@keyframes _frame_slot_kf {
  0% {
    --slot-width: 5%;
  }
  10% {
    --slot-width: 10%;
  }
  20% {
    --slot-width: 20%;
  }
  30% {
    --slot-width: 30%;
  }
  40% {
    --slot-width: 40%;
  }
  50% {
    --slot-width: 50%;
  }
  60% {
    --slot-width: 60%;
  }
  70% {
    --slot-width: 70%;
  }
  80% {
    --slot-width: 80%;
  }
  90% {
    --slot-width: 90%;
  }
  100% {
    --slot-width: 100%;
  }
}
